.App {
  text-align: center;
  min-height: 100vh;
  background: #f5f7fa;
}

.main-nav {
  background: #fff;
  border-bottom: 1px solid #eee;
  padding: 10px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.03);
}
.nav-list {
  display: flex;
  justify-content: center;
  gap: 24px;
  list-style: none;
  margin: 0;
  padding: 0;
}
.nav-btn {
  background: #fff;
  color: #3949ab;
  border: 1px solid #3949ab;
  border-radius: 6px;
  padding: 8px 20px;
  font-weight: 500;
  cursor: pointer;
  font-size: 16px;
  transition: background 0.2s, color 0.2s;
}
.nav-btn.active,
.nav-btn:hover {
  background: #3949ab;
  color: #fff;
}

.main-content {
  min-height: 80vh;
  max-width: 900px;
  margin: 0 auto;
  padding: 32px 0 0 0;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
