
.api-setup-container {
	max-width: 400px;
	margin: 40px auto;
	padding: 24px;
	border: 1px solid #eee;
	border-radius: 8px;
	background: #fff;
	box-shadow: 0 2px 8px rgba(0,0,0,0.04);
}

.api-setup-container h2 {
	margin-bottom: 18px;
	font-size: 1.5rem;
	color: #283593;
}

.api-setup-field {
	margin-bottom: 16px;
}

.api-setup-field label {
	font-weight: 500;
	color: #222;
}

.api-setup-field input {
	width: 100%;
	padding: 8px;
	margin-top: 4px;
	border: 1px solid #bbb;
	border-radius: 4px;
	font-size: 1rem;
	box-sizing: border-box;
}

.api-setup-btn {
	padding: 8px 16px;
	background: #3949ab;
	color: #fff;
	border: none;
	border-radius: 4px;
	font-size: 1rem;
	cursor: pointer;
	transition: background 0.2s;
}
.api-setup-btn:hover {
	background: #283593;
}

.api-setup-success {
	color: green;
	margin-top: 12px;
	font-weight: 500;
}
